.top-wrapper {
  padding: 1em 0;
  @include container();
  &__black {
    background-color: $dark-black;
  }
}

@include susy-breakpoint($b-medium, 12) {
  .site-header-top {
    @include container($container-width);
    &__s-logo {
      @include span(3);
    }
    &__nav {
      @include span(9 last);
      text-align: right;
    }
    &__ham-btn {
      display: none;
    }
  }
}

